import React from "react";
import styled from "styled-components";

// 
const addButtonColor = "var(--tg-theme-button-color)";
const addHoverColor = "var(--tg-theme-hint-color)";
const addTextColor = "var(--tg-theme-button-text-color)";

const removeButtonColor = "tomato";
const removeHoverColor = "rgb(209, 83, 61)";

const checkoutButtonColor = "rgb(38, 121, 38)";
const checkoutHoverColor = "rgb(41, 141, 41)";

const Btn = styled.button`
  display: inline-block;
  background-color: ${(props) => {
    switch (props.type) {
      case "add":
        return addButtonColor;
      case "remove":
        return removeButtonColor;
      case "checkout":
        return checkoutButtonColor;
      default:
        return "transparent";
    }
  }};
  color: ${(props) => {
    switch (props.type) {
      case "add":
        return addTextColor;
      default:
        return "white";
    }
  }};
  outline: none;
  border: none;
  padding: 0.6rem 0.8rem;
  font-size: 1.2rem;
  text-align: center;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.2s ease;
  position: flex;

  &:hover {
    transform: scale(0.9);
    background-color: ${(props) => {
      switch (props.type) {
        case "add":
          return addHoverColor;
        case "remove":
          return removeHoverColor;
        case "checkout":
          return checkoutHoverColor;
        default:
          return "transparent";
      }
    }};
  }

  &::after {
    content: " ";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${(props) => props.theme.text};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transition: all 0.2s ease;
  }

  &:hover::after {
    transform: translate(-50%, -50%) scale(1);
    padding: 0.3rem;
  }
`;

function Button({ type, title, disable, onClick }) {
  return (
    <Btn className={`${type}`} type={type} disabled={disable} onClick={onClick}>
      {title}
    </Btn>
  );
}

export default Button;
